export function buildFeignImageCSS(): string {
  // Feign icon images
  const data = [
    ":root {",
    "  --feign-icon-blue: url(data:image/webp;base64,);",
    "  --feign-icon-brown: url(data:image/webp;base64,);",
    "  --feign-icon-green: url(data:image/webp;base64,);",
    "  --feign-icon-lime: url(data:image/webp;base64,);",
    "  --feign-icon-orange: url(data:image/webp;base64,);",
    "  --feign-icon-pink: url(data:image/webp;base64,);",
    "  --feign-icon-purple: url(data:image/webp;base64,);",
    "  --feign-icon-red: url(data:image/webp;base64,);",
    "  --feign-icon-rose: url(data:image/webp;base64,);",
    "  --feign-icon-salmon: url(data:image/webp;base64,);",
    "  --feign-icon-sky: url(data:image/webp;base64,);",
    "  --feign-icon-white: url(data:image/webp;base64,);",
    "  --feign-icon-yellow: url(data:image/webp;base64,);",
    "}",
  ];
  return data.join('\n');
}
